@import "../../global.scss";
.works{
    background-color: #B1D0E0;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    .arrow {
        height: 50px;
        position: absolute;
        cursor: pointer;


        &.left {
            left: 100px;
            transform: rotateY(180deg);
        }
         &.right {
            right: 100px;
        }
    }

    .slider {
        height: 350px;
        display: flex;
        position: absolute;
        left: 0;
        transition: all 1s ease-out;


        .container {
            width: 100vw;
            display: flex;
            align-items: center;
            justify-content: center;

            .item {
                width: 700px;
                height: 100%;
                background-color: white;
                border-radius: 20px;
                display: flex;
                align-items: center;
                justify-content: center;

                @include mobile{
                    width:250px;
                }

                .left {
                    flex: 4;
                    height: 80%;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    .leftContainer{
                        width: 90%;
                        height: 70%;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;

                        .imgContainer {
                            width: 40%;
                            height: 40%;
                            border-radius: 50%;
                            background-color: rgb(245, 179, 155);
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            @include mobile{
                                width: 25%;
                                height: 25px;
                            }


                            img {
                                width: 25px;
                                 @include mobile{
                                     width:15px;
                                    }
                            }
                        }
                        h2 {
                            font-size: 20px;
                            @include mobile{
                                 font-size: 10px;
                            }
                        }

                        p {
                            font-size: 13px;
                            @include mobile{
                                font-size: 9px;
                            }
                        }
                        span {
                            font-size: 12px;
                            font-weight: 600;
                            text-decoration: underline;
                            cursor: pointer;
                            @include mobile{
                                font-size: 9px;
                            }
                        }
                    }
                }
                .right {
                    flex: 8;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    overflow: hidden;

                    img {
                        width: 400px;
                        transform: rotate(-10deg);
                    }
                }
            }
        }
    }    
}