@import "../../global.scss";

.portfolio{
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;

    h1 {
        font-size: 50px;
         @include mobile{
            font-size: 25px;
            height: 50px;
        }
    }

    ul {
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;
    }
    .container {
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        @include mobile{
            overflow-y:hidden;
        
        }
        .item {
            width: 250px;
            height: 200px;
            border-radius: 20px;
            border: 1px solid lightgray;
            margin: 40px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;
            transition: all 0.5s ease;
            cursor: pointer;

            h3 {
                position: absolute;
                font-size: 20px;
                 @include mobile{
                    font-size: 10px;
                }
            }
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1;
            }

            &:hover {
                background-color: $maincolor;
                img {
                    opacity: 0.2;
                    z-index: 0;
                }
            }
        }
    }
}